  padding: 1em;
  position: relative;
  font-weight: bold;
  font-size: 20px;
}
.product-card__item:before {
  content: "";
  background: url("../img/bg.jpg");
  position: absolute;
  bottom: 0;
  left: 0;
  height: 80%;
  width: 100%;
  z-index: -1;
  border-radius: 10px;
}

.production-step {
  display: grid;
  align-items: center;
  gap: var(--lg);
  margin-bottom: calc(2 * var(--lg));
  /* .production-step__cnt */
  /* .production-step__title */
  /* .production-step__txt */
  /* .production-step__link */
  /* .production-step__img */
}
@media (min-width: 767px) {
  .production-step {
    grid-template-columns: 40% 1fr;
  }
}
.production-step__cnt-wrap {
  max-width: 450px;
  margin: auto;
}
@media (min-width: 1023px) {
  .production-step__txt {
    font-size: 18px;
  }
}
.production-step__link {
  margin-top: 3em;
}
.production-step__img {
  text-align: center;
  position: relative;
}
.production-step--vert {
  text-align: center;
  align-content: center;
  grid-template-columns: 1fr;
}
@media (max-width: 767px) {
  .production-step--reverse {
    order: 1;
  }
}

.fill-block {
  background-color: #ebebeb;
  position: relative;
}
@media (min-width: 767px) {
  .fill-block {
    grid-template-columns: 1fr 1fr;
  }
}
.fill-block .production-step__cnt-wrap {
  padding: var(--lg);
  max-width: 600px;
}
.fill-block img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 767px) {
  .fill-block.round-top .fill-block__img {
    order: -1;
  }
}

.map {
  padding: 20px;
  text-align: center;
  background: url("../img/map-grid.svg");
  background-size: cover;
}

.production-step__benefits {
  padding: 0 var(--lg) var(--lg);
}

.choices {
  position: relative;
}
.choices:focus {
  outline: none;
}
.choices__inner {
  position: relative;
  padding-right: 0.75em;
  cursor: pointer;
}
.choices__inner:before {
  content: "";
  position: absolute;
  width: 0.5em;
  height: 0.5em;
  background: url("../img/icon/dropdown.svg") no-repeat center center;
  background-size: cover;
  right: 0;
  top: 0.7em;
}
.choices__item {
  padding: 0.25em;
  line-height: 1.2;
  cursor: pointer;
}
.choices__list--dropdown {
  min-width: 100%;
  position: absolute;
  z-index: 100;
  background-color: #fff;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  display: none;
}
.choices__list--dropdown.is-active {
  display: block;
}
.choices .is-highlighted {
  background-color: #FD9F32;
}

.intro-slider .swiper-pagination {
  top: 80px;
  left: 60px;
}
.intro-slider .swiper-slide:not(.swiper-slide-active) {
  opacity: 0 !important;
}

.swiper-pagination {
  display: flex;
  position: absolute;
  z-index: 2;
}

.swiper-pagination-bullet {
  position: relative;
  width: 10px;
  height: 10px;
  margin: 7px;
  border-radius: 50%;
  background-color: #B69D86;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #FD9F32;
}
.swiper-pagination-bullet.swiper-pagination-bullet-active:before {
  border-color: #FD9F32;
  width: 180%;
  height: 180%;
}
.swiper-pagination-bullet:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  height: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  border-radius: 50%;
  transition: width 0.3s, height 0.3s;
}