.search {
  @include rmax(600) {
    display: none;
  }
  //.search__btn
  &__btn {
    border: none;
    background-color: transparent;
    appearance: none;
    color: $c-text;
    font-size: 20px;
  }
  &__soc {
    margin-top: -.5em;
  }
}

.header {
  position: relative;
  margin-bottom: $lg;
  &::after {
    content: "";
    width: 100%;
    height: 1px;
    background-color: #A4A4A4;
    display: block;
    position: absolute;
    top: 60px;
  }
  //header__lang
  &__lang {
    width: 45px;
    @include rmax(600) {
      display: none;
    }
  }
  //header__logo
  &__logo {
    flex: 0 0 300px;
    position: relative;
    top: 20px;
    background-color: #f7f7f7;
    z-index: 1;
    padding: 0 20px;
  }
}

.mob-header {
  text-align: center;
  .soclist {
    margin-bottom: 1em;
  }
}

.soclist {
  text-align: center;
}

.mini-cart {
  position: relative;
  padding: 5px;
  border-radius: 3px;
  font-size: 20px;
  display: inline-block;
  /* .mini-cart__count */
  &__count {
    color: #fff;
    position: absolute;
    width: 16px;
    height: 16px;
    bottom: 0;
    right: 0;
    border-radius: 50%;
    background-color: $c-main;
    padding: 2px;
    font-size: 10px;
    font-weight: bold;
    text-align: center;
    transform: translate(4px, 4px);
  }
}
.soclist {
  //.soclist__item
  &__item {
    display: inline-block;
    text-decoration: none;
    font-size: 24px;
    line-height: 0;
    color: $c-sec;
    padding: 0 2px;
  }
}

.link-arr-hover {
  span {
    position: relative;
    transition: all .3s;
    &:before {
      content: "";
      position: absolute;
      right: 0;
      bottom: .1em;
      width: 1em;
      height: 1em;
      background: url('@/img/icon/arrow.svg') no-repeat;
      background-size: cover;
      transition: all .3s;
      opacity: 0;
    }
  }
  &:hover {
    span {
      transform: translateX(-.5em);
      &:before {
        transform: translateX(1.2em);
        opacity: 1;
      }
    }
  }
}

.link-arr {
  color: $c-main;
  font-weight: 800;
  text-decoration: none;
  white-space: nowrap;
  display: inline-flex;
  align-items: center;
  gap: .5em;
  .iconsvg {
    font-size: .75em;
  }
}

.round-both {
  mask: var(--mask-both);
}
.round-bottom{
  mask: var(--mask-bottom);
}
.round-top{
  mask: var(--mask-top);
}
.round-left{
  mask: var(--mask-left);
}

.bg-card {
    background: url('@/img/bg.jpg');
    padding: calc(2*$lg);
}


.video {
  width: 100%;
  max-width: 420px;
  &__wrap {
    position: relative;
    display: block;
    border-radius: 6px;
    overflow: hidden;
    padding-bottom: 56.6%;
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000, .3);
      opacity: .5;
      z-index: 1;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      left: 0;
      top: 0;
    }
    &:hover {
      &:before {
        opacity: 1;
      }
      .video__link:after {
        animation: video .5s infinite alternate;
      }
    }
  }

  /* .video__link */
  &__link {
    position: absolute;
    color: #fff;
    font-size: 54px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 5;
    line-height: 0;
    border-radius: 50%;
    &::before, &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      border: 1px solid #FFEDD7;
      left: 0;
      top: 0;
      transition: all .3s;
    }
    &:before {
      transform: scale(1.5);
    }
    &:after {
      opacity: 0;
    }

  }
}

.marker {
  display: none;
  width: 26px;
  height: 26px;
  background: url('@/img/icon/dropdown.svg') no-repeat center center;
  background-size: contain;
  transition: all .3s;
}

@keyframes video {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: scale(1.5);
  }
}

.sec-nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  gap: 1em;
  justify-content: space-between;

  @include r(1280) {
    flex-direction: column;
  }

  @include rmax(767) {
    display: none;
  }

  /* .sec-nav__item */
  &__item {}

  /* sec-nav__link */
  &__link {
    text-decoration: none;
    font-size: 18px;
    color: $c-sec;
    font-weight: 700;
    display: inline-flex;
    align-items: center;
    transition: all .3s;

    @include r(1023) {
      font-size: 22px;
    }

    &:hover {
      color: $c-main;
    }

    &.active {
      color: $c-main;
    }

    &.active:before {
      content: "";
      display: inline-block;
      width: 1em;
      height: 1em;
      margin-right: .5em;
      border-radius: 50%;
      border: 1px solid $c-main;
      background: radial-gradient($c-main 30%, #fff 30%);
    }
  }
}

.intro {
  display: grid;
  gap: 3vw;
  align-items: start;

  @include r(1280) {
    grid-template-columns: 300px 1fr;
  }
  /* .intro__nav */
  &__nav {}
  /* .intro__slide */
  &__slide {
    background: url("@/img/bg.jpg");
    @include r(767) {
      display: grid;
      gap: $lg;
      grid-template-columns: 1fr 1fr;
    }
  }

  /* .intro__slide-cnt */
  &__slide-cnt {
    padding: 40px;
    align-self: center;
  }

  /* .intro__title */
  &__title {
    font-size: 26px;
    font-weight: 700;
    margin-bottom: 1em;
    line-height: 1;
    @include r(1023) {
      font-size: 36px;
    }
  }

  /* .intro__slide-img */
  &__slide-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    &.round-left {
      @include rmax(767) {
        mask: var(--mask-top)
      }
    }
  }
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 18px;
  @include r(1024) {
    font-size: 22px;
  }
  /* .list__item */
  &__item {
    padding-left: 1em;
    position: relative;
    &:not(:last-child) {
      margin-bottom: 1em;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0.35em;
      left: 0;
      border-radius: 50%;
      background-color: $c-main;
      width: .5em;
      height: .5em;
    }
  }
}



